
$mainColor: #3617ce;


//mixin
@mixin NotoSans () {
	font-family: 'Noto Sans KR', sans-serif; }


header {
	position: absolute;
	top: 0;
	left: 0;
	padding: 20px;
	display: flex;
	justify-content: space-between;
	width: 100%;
	z-index: 50;
	h1 {
		a {
			height: 30px;
			display: block;
			img {
				width: 80px;
				height: 30px; } } }
	// siteMap
	nav {
		& > a {
			display: block;
			text-indent: -9999px;
			width: 36px;
			height: 36px;
			position: relative;
			border-radius: 50%;
			background: url("../images/menu.png") no-repeat 0 0; }

		& > div {
			overflow-y: scroll;
			display: none;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			background-color: $mainColor;
			padding: 20px 5.3%;
			animation: opacity 1s both;


			&.on {
				display: block; }
			&::before {
				content: "";
				background: url("../images/logo_main.png") no-repeat 0 0;
				background-size: 80px 30px;
				position: absolute;
				top: 20px;
				left: 5.3%;
				width: 80px;
				height: 30px; }

			.btn-drawer-close {
				position: absolute;
				top: 20px;
				right: 5.3%;
				width: 36px;
				height: 36px;
				background-color: #ffffff;
				border-radius: 50%;
				text-indent: -9999px;
				&::before {
					content: "";
					width: 20px;
					height: 1px;
					background-color: #000;
					position: absolute;
					top: 17px;
					left: 8px;
					transform: rotate(-45deg); }
				&::after {
					content: "";
					width: 20px;
					height: 1px;
					background-color: #000;
					position: absolute;
					top: 17px;
					left: 8px;
					transform: rotate(45deg); } }
			ul {
				padding-top: 35%;
				li {
					a {
						display: block;
						font-size: 32px;
						color: #ffffff;
						letter-spacing: -0.64px;
						line-height: 1.6;
						font-family: 'Noto Sans KR bold'; } } }

			.logoutBtn {
				margin-top: 179px;
				font-size: 16px;
				line-height: 25px;
				letter-spacing: -0.32px;
				font-family: 'Noto Sans KR bold';
				color: #ffffff;
				margin-top: 30px;
				display: block; }
			p {
				margin-top: 123px;
				font-size: 16px;
				line-height: 25px;
				letter-spacing: -0.32px;
				font-family: 'Noto Sans KR bold';
				span {
					font-weight: normal; } } } } }


